@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
  margin: 0;
  font-family: "Lato";
  color: #333;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
  margin-right: 25px;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #630BFF;
}


.heading-container {
  padding-top: 21px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.heading-container img{
  margin-right: 20px;
  height: 25px;
  width: 25px;
}

.content-container {
  display: flex;
  align-items: flex-start;
  background-color: #FFFCEF;
}

.content{
  margin-left: 350px;
  margin-right: 370px;
  text-align: left;
  max-width: 600px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.add-assignment-title h1{
    margin-top: 15px;
    margin-bottom: 20px;
    font-size: 25px;
  }
  
/*For Construct*/
.question-assignment {
    background-color:white;
    width: 650px;
    padding-left: 15px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-radius: 10px;
  }
  
  .question-assignment-title h2{
    padding-top: 20px;
    padding-bottom: 23px;
    margin-top: 15px;
    font-size: 17px;
    font-weight: 400;
  }
  
  .form-check {
    margin-bottom: 10px;
    align-items: center;
    justify-content: center;
    align-self: center;
  }
  
  .form-check-label {
    display: block;
    cursor: pointer;
  }
  
  .form-check-input{
    margin-right: 10px;
    height: 17px;
    width: 17px;
    vertical-align: middle;
    position: relative;
    cursor: pointer;
  }
  
  /*For Title*/
  .question-assignment-title-exc h2{
    padding-top: 20px;
    padding-bottom: 15px;
    margin-top: 15px;
    font-size: 17px;
    font-weight: 400;
  }
  
  .assignment-title-text input{
    width: 95%;
    padding: 2px 2px;
    margin: 0 0;
    height: 20px;
    border-bottom-width: 1px;
    border-bottom-color: gray;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    display: block;
    font-size: 17px;
  }
  
  .assignment-title-text input:focus{
    outline: none;
  }
  
  
  .assignment-text-reference {
    display: flex;
    flex-direction: row;
    align-items: baseline;
  }
  
  .assignment-text-reference input{
    width: 84%;
    padding: 2px 2px;
    margin: 0 0;
    height: 20px;
    border-bottom-width: 1px;
    border-bottom-color: gray;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    display: block;
    font-size: 17px;
  }
  
  .assignment-text-reference input:focus{
    outline: none;
  }
  
  .assignment-text-reference h2{
    padding-bottom: 15px;
    margin-top: 10px;
    font-size: 17px;
    font-weight: 400;
    margin-right: 7px;
  }
  
  
  .assignment-text-reference-name {
    display: flex;
    flex-direction: row;
    align-items: baseline;
  }
  
  .assignment-text-reference-name input{
    width: 62.5%;
    padding: 2px 2px;
    margin: 0 0;
    height: 20px;
    border-bottom-width: 1px;
    border-bottom-color: gray;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    display: block;
    font-size: 17px;
  }
  
  .assignment-text-reference-name input:focus{
    outline: none;
  }
  
  .assignment-text-reference-name h2{
    padding-bottom: 15px;
    margin-top: 10px;
    font-size: 17px;
    font-weight: 400;
    margin-right: 7px;
  }
  
  .submit-assignment{
    padding-top: 10px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .submit-assignment button{
    background: #630BFF;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
    align-self: center;
    font-size: 15px;
    height: 40px;
    width: 170px;
  }  
.home h1{
    font-size: 15px;
    margin-top: 15px;
    margin-bottom: 5px;
    margin-left: 5px;
    color: #B8B298;
  }

.assignment-preview {
    padding: 10px 16px;
    /* margin: 20px 0; */
    margin-bottom: 20px;
    border-bottom: 1px solid #fafafa;
    border-radius: 15px;
    background-color: white;
    /* cursor: pointer; */
  }
  .assignment-preview:hover {
    box-shadow: 1px 3px 5px rgba(71, 55, 55, 0.1);
  }
  .assignment-preview h2 {
    font-size: 20px;
    /* color: #f1356d; */
    margin-bottom: 8px;
  }
  .assignment-preview a {
    text-decoration: none;
  }
  
  .assignment-preview p{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
            line-clamp: 3; 
    -webkit-box-orient: vertical;
  } 
  
  .assignment-preview .career-name{
    font-size: 13px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 3px;
    padding-bottom: 3px;
    margin-bottom: 20px;
    background-color: #eeeeee;
    color: #630BFF;
    display: inline-block;
  }
  
  .assignment-edit img{
    margin-right: 20px;
    height: 20px;
    width: 20px;
    margin-left: auto;
    display: flex;
    justify-content: flex-end;
  }
  
  .assignment-preview-header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* justify-content: center; */
  }
